// 日本語の単語リスト（簡単なもの）
const japaneseWords = [
  'りんご', 'ごりら', 'らっぱ', 'ぱん', 'どらむ', 'むすこ', 'こま', 'まくら', 'らいおん', 
  'すいか', 'かもめ', 'めだか', 'かめ', 'めがね', 'ねこ', 'こい', 'いぬ', 'ぬりえ', 'えほん',
  'ほん', 'にんじん', 'じどうしゃ', 'くるま', 'まり', 'りす', 'すずめ', 'めろん', 'にく', 'くつ',
  'つみき', 'きつね', 'ねずみ', 'みかん', 'かんづめ', 'めがみ', 'みみ', 'みず', 'ずかん',
  'かさ', 'さかな', 'なべ', 'べる', 'るす', 'すいとう', 'うし', 'しまうま', 'まほう', 'うさぎ',
  'ぎんこう', 'うま', 'まんが', 'がちょう', 'うで', 'でんわ', 'わに', 'にもつ', 'つる', 'るびー',
  'いす', 'かい', 'いるか', 'さい', 'いか', 'かばん', 'ばんそうこう', 'うえ', 'えび',
  'びん', 'ふうせん', 'せんぷうき', 'きんぎょ', 'よっと', 'とら', 'らくだ', 'だちょう', 'まんと',
  'とまと', 'とんぼ', 'ぼうし', 'まど', 'どうぶつ', 'きりん', 'あひる', 'るすばん', 'ん',
  'あし', 'いか', 'うさぎ', 'えんぴつ', 'おにぎり', 'かめ', 'きりん', 'くま', 'けむし', 'こあら',
  'さる', 'しか', 'すいか', 'せみ', 'ぞう', 'たいこ', 'ちきゅう', 'つき', 'てんとうむし', 'とけい',
  'ないふ', 'にわとり', 'ぬいぐるみ', 'ねずみ', 'のり', 'はさみ', 'ひよこ', 'ふくろう', 'へび', 'ほうき',
  'まくら', 'みかん', 'むし', 'めがね', 'もち', 'やかん', 'ゆきだるま', 'ようふく', 'らくだ', 'りぼん',
  'るびー', 'れもん', 'ろうそく', 'わに', 'さくらんぼ', 'ぶどう', 'いちご', 'ばなな', 'もも', 'ぱいなっぷる',
  'てれび', 'らじお', 'ぱそこん', 'けいたい', 'でんち', 'かめら', 'まいく', 'すぴーかー', 'へっどほん',
  'きーぼーど', 'まうす', 'つくえ', 'いす', 'かびん', 'ごみばこ', 'はぶらし', 'たおる', 'せっけん',
  'しゃんぷー', 'りんす', 'かがみ', 'くし', 'どらいやー', 'れいぞうこ', 'せんたくき', 'そうじき',
  'でんしれんじ', '炊飯器（すいはんき）', 'しょっき', 'ふらいぱん', 'なべ', 'おたま', 'しゃもじ',
  'はし', 'すぷーん', 'ふぉーく', 'ちゃわん', 'こっぷ', 'さら', 'まないた', 'ほうちょう', 'やかん'
];

const ShiritoriGame = ({ onGameOver }) => {
  const [currentWord, setCurrentWord] = useState('');
  const [inputWord, setInputWord] = useState('');
  const [wordHistory, setWordHistory] = useState([]);
  const [score, setScore] = useState(0);
  const [timeLeft, setTimeLeft] = useState(30);
  const [gameActive, setGameActive] = useState(false);
  const [gameStarted, setGameStarted] = useState(false);
  const [playerTurn, setPlayerTurn] = useState(true); // true: プレイヤー, false: CPU

  useEffect(() => {
    if (!gameStarted) return;

    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          setGameActive(false);
          onGameOver(score);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, [gameStarted, score, onGameOver]);

  useEffect(() => {
    if (!playerTurn && gameActive && currentWord) {
      // CPUのターン
      setTimeout(() => {
        const lastChar = currentWord.slice(-1);
        const possibleWords = japaneseWords.filter(word => 
          word.startsWith(lastChar) && 
          !wordHistory.includes(word) &&
          word.length >= 2
        );
        
        if (possibleWords.length > 0) {
          const cpuWord = possibleWords[Math.floor(Math.random() * possibleWords.length)];
          setCurrentWord(cpuWord);
          setWordHistory(prev => [...prev, cpuWord]);
          setPlayerTurn(true);
        } else {
          // CPUが答えられない場合
          setGameActive(false);
          onGameOver(score + 50); // 勝利ボーナス
        }
      }, 1500);
    }
  }, [playerTurn, gameActive, currentWord, wordHistory, score, onGameOver]);

  const startGame = () => {
    const initialWord = japaneseWords[Math.floor(Math.random() * japaneseWords.length)];
    setCurrentWord(initialWord);
    setWordHistory([initialWord]);
    setScore(0);
    setTimeLeft(30);
    setGameActive(true);
    setGameStarted(true);
    setPlayerTurn(false); // CPUから開始
    setInputWord('');
  };

  const submitWord = () => {
    if (!inputWord.trim() || !gameActive || !playerTurn) return;

    const lastChar = currentWord.slice(-1);
    const newWord = inputWord.trim();

    // バリデーション
    if (newWord.length < 2) {
      alert('2文字以上の単語を入力してください');
      return;
    }

    if (!newWord.startsWith(lastChar)) {
      alert(`「${lastChar}」で始まる単語を入力してください`);
      return;
    }

    if (wordHistory.includes(newWord)) {
      alert('その単語は既に使われています');
      return;
    }

    if (!japaneseWords.includes(newWord)) {
      alert('有効な日本語の単語を入力してください');
      return;
    }

    // 有効な単語の場合
    setCurrentWord(newWord);
    setWordHistory(prev => [...prev, newWord]);
    setScore(prev => prev + 10);
    setInputWord('');
    setPlayerTurn(false);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      submitWord();
    }
  };

  const resetGame = () => {
    setGameStarted(false);
    setGameActive(false);
    setCurrentWord('');
    setInputWord('');
    setWordHistory([]);
    setScore(0);
    setTimeLeft(30);
    setPlayerTurn(true);
  };

  const getLastLetter = () => {
    return currentWord ? currentWord.slice(-1) : '';
  };

  return (
    <GameContainer>
      <HomeButton onClick={() => onGameOver(score)}>ホームに戻る</HomeButton>
      <GameArea>
        <Title>しりとりバトル</Title>
        <Score>スコア: {score}</Score>
        <Timer timeLeft={timeLeft}>残り時間: {timeLeft}秒</Timer>
        
        {!gameStarted ? (
          <Button onClick={startGame}>ゲーム開始</Button>
        ) : (
          <>
            <CurrentWord>現在の単語: {currentWord}</CurrentWord>
            <LastLetter>{getLastLetter()}</LastLetter>
            
            <PlayerTurn>
              {playerTurn ? 'あなたの番です' : 'CPUが考え中...'}
            </PlayerTurn>
            
            {playerTurn && gameActive && (
              <InputContainer>
                <Input
                  type="text"
                  value={inputWord}
                  onChange={(e) => setInputWord(e.target.value)}
                  onKeyPress={handleKeyPress}
                  placeholder={`「${getLastLetter()}」で始まる単語`}
                  disabled={!playerTurn}
                />
                <Button onClick={submitWord} disabled={!inputWord.trim()}>
                  送信
                </Button>
              </InputContainer>
            )}
            
            <WordHistory>
              <strong>単語履歴:</strong>
              {wordHistory.map((word, index) => (
                <WordItem key={index}>
                  {index + 1}. {word}
                </WordItem>
              ))}
            </WordHistory>
            
            {!gameActive && timeLeft === 0 && (
              <>
                <GameOver>ゲーム終了！</GameOver>
                <Button onClick={resetGame}>もう一度プレイ</Button>
              </>
            )}
          </>
        )}
      </GameArea>
    </GameContainer>
  );
};

export default ShiritoriGame; 