import React, { useState, useEffect, useCallback } from 'react';
import styled, { keyframes } from 'styled-components';

const GameContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  height: 100vh;
  background-color: #f0f8ff;
  position: relative;
`;

const ClickerButton = styled.button`
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: #e94560;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  transition: transform 0.1s, background 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;

  &:hover {
    background: #d13b54;
  }

  &:active {
    transform: scale(0.95);
  }
`;

const Stats = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: white;
  font-size: 1.2rem;
  text-align: center;
`;

const UpgradeButton = styled.button`
  background: #ffc107;
  color: black;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1em;
  cursor: pointer;
  margin: 5px;

  &:hover {
    background: #ffca28;
  }

  &:disabled {
    background: #ccc;
    cursor: not-allowed;
  }
`;

const GameOver = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.8);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  color: white;
`;

const HomeButton = styled.button`
  position: absolute;
  bottom: 20px;
  right: 20px;
  background: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1.5em;
  cursor: pointer;
  z-index: 10;
  &:hover {
    background: #45a049;
  }
`;

const Score = styled.div`
  font-size: 3em;
  color: black;
`;

const Timer = styled.div`
  font-size: 1.5em;
  color: black;
`;

const StatsContainer = styled.div`
  color: black;
  font-size: 1.2em;
  text-align: center;
`;

function ClickerGame({ onGameOver }) {
  const [clicks, setClicks] = useState(0);
  const [clickPower, setClickPower] = useState(1);
  const [autoClickers, setAutoClickers] = useState(0);
  const [gameOver, setGameOver] = useState(false);
  const [timeLeft, setTimeLeft] = useState(30);

  const handleClick = useCallback(() => {
    setClicks(prev => prev + clickPower);
  }, [clickPower]);

  const buyUpgrade = useCallback((type) => {
    const costs = {
      power: clickPower * 10,
      auto: (autoClickers + 1) * 50
    };

    if (clicks >= costs[type]) {
      setClicks(prev => prev - costs[type]);
      if (type === 'power') {
        setClickPower(prev => prev + 1);
      } else {
        setAutoClickers(prev => prev + 1);
      }
    }
  }, [clicks, clickPower, autoClickers]);

  useEffect(() => {
    const autoClickInterval = setInterval(() => {
      if (autoClickers > 0) {
        setClicks(prev => prev + autoClickers);
      }
    }, 1000);

    return () => clearInterval(autoClickInterval);
  }, [autoClickers]);

  useEffect(() => {
    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          setGameOver(true);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const handleRestart = () => {
    setClicks(0);
    setClickPower(1);
    setAutoClickers(0);
    setGameOver(false);
    setTimeLeft(30);
  };

  const handleExit = () => {
    onGameOver(clicks);
  };

  return (
    <GameContainer>
      <HomeButton onClick={() => onGameOver(clicks)}>ホームに戻る</HomeButton>
      <Score>スコア: {clicks}</Score>
      <Timer>残り時間: {timeLeft}秒</Timer>
      <StatsContainer>
        <p>クリック力: {clickPower}</p>
        <p>自動クリッカー: {autoClickers}</p>
      </StatsContainer>
      <ClickerButton onClick={handleClick} disabled={gameOver}>
        クリック！
      </ClickerButton>
      <div style={{ display: 'flex', gap: '10px' }}>
        <UpgradeButton
          onClick={() => buyUpgrade('power')}
          disabled={clicks < clickPower * 10 || gameOver}
        >
          クリック力アップ ({clickPower * 10}ポイント)
        </UpgradeButton>
        <UpgradeButton
          onClick={() => buyUpgrade('auto')}
          disabled={clicks < (autoClickers + 1) * 50 || gameOver}
        >
          自動クリッカー購入 ({(autoClickers + 1) * 50}ポイント)
        </UpgradeButton>
      </div>
      {gameOver && (
        <GameOver>
          <h2>ゲーム終了！</h2>
          <p>最終スコア: {clicks}ポイント</p>
          <button onClick={handleRestart}>もう一度プレイ</button>
          <button onClick={handleExit} style={{ marginLeft: '10px' }}>終了</button>
        </GameOver>
      )}
    </GameContainer>
  );
}

export default ClickerGame; 