import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';

const GameContainer = styled.div`
  width: 100vw;
  height: 100vh;
  background: linear-gradient(135deg, #87CEEB 0%, #98FB98 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #333;
  position: relative;
`;

const HomeButton = styled.button`
  position: absolute;
  bottom: 20px;
  right: 20px;
  background: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  z-index: 10;
  &:hover {
    background: #45a049;
  }
`;

const GameBoard = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin: 20px;
`;

const MoleHole = styled.div`
  width: 120px;
  height: 120px;
  background: #8B4513;
  border-radius: 50%;
  border: 4px solid #654321;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.2s ease;
  
  &:hover {
    transform: scale(1.05);
  }
`;

const Mole = styled.div`
  width: 80px;
  height: 80px;
  background: #8B7355;
  border-radius: 50%;
  position: absolute;
  bottom: ${props => props.isVisible ? '10px' : '-100px'};
  transition: bottom 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  
  &::before {
    content: '👹';
    font-size: 50px;
  }
`;

const Bomb = styled(Mole)`
  &::before {
    content: '💣';
  }
`;

const Score = styled.div`
  font-size: 32px;
  font-weight: bold;
  margin: 20px;
  color: #333;
`;

const Timer = styled.div`
  font-size: 24px;
  margin: 10px;
  color: #333;
`;

const Button = styled.button`
  background: #4CAF50;
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 25px;
  font-size: 18px;
  cursor: pointer;
  margin: 20px;
  transition: all 0.3s ease;
  
  &:hover {
    background: #45a049;
    transform: scale(1.05);
  }
`;

const GameOver = styled.div`
  font-size: 48px;
  font-weight: bold;
  color: #ff6b6b;
  margin: 20px;
`;

const WhackAMoleGame = ({ onGameOver }) => {
  const [score, setScore] = useState(0);
  const [timeLeft, setTimeLeft] = useState(30);
  const [gameActive, setGameActive] = useState(false);
  const [holes, setHoles] = useState(Array(9).fill(null));
  const [gameStarted, setGameStarted] = useState(false);
  const moleIntervalRef = useRef(null);
  const gameTimerRef = useRef(null);

  useEffect(() => {
    if (!gameStarted) return;

    // ゲームタイマー
    gameTimerRef.current = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          setGameActive(false);
          onGameOver(score);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    // モグラ出現タイマー
    moleIntervalRef.current = setInterval(() => {
      if (gameActive) {
        setHoles(prev => {
          const newHoles = [...prev];
          const emptyHoles = newHoles.map((h, i) => h === null ? i : -1).filter(i => i !== -1);
          if (emptyHoles.length === 0) return newHoles;
          
          const randomIndex = emptyHoles[Math.floor(Math.random() * emptyHoles.length)];
          const isBomb = Math.random() < 0.2; // 20%の確率で爆弾
          newHoles[randomIndex] = isBomb ? 'bomb' : 'mole';
          
          // 1.5秒後に隠す
          setTimeout(() => {
            setHoles(current => {
              const updated = [...current];
              if (updated[randomIndex] === (isBomb ? 'bomb' : 'mole')) {
                 updated[randomIndex] = null;
              }
              return updated;
            });
          }, 1500);
          
          return newHoles;
        });
      }
    }, 800);

    return () => {
      if (moleIntervalRef.current) clearInterval(moleIntervalRef.current);
      if (gameTimerRef.current) clearInterval(gameTimerRef.current);
    };
  }, [gameStarted, gameActive, score, onGameOver]);

  const startGame = () => {
    setScore(0);
    setTimeLeft(30);
    setGameActive(true);
    setGameStarted(true);
    setHoles(Array(9).fill(null));
  };

  const whackHole = (index) => {
    if (holes[index] && gameActive) {
      if (holes[index] === 'mole') {
        setScore(prev => prev + 10);
        setHoles(prev => {
          const newHoles = [...prev];
          newHoles[index] = null;
          return newHoles;
        });
      } else if (holes[index] === 'bomb') {
        setGameActive(false);
        onGameOver(score);
      }
    }
  };

  const resetGame = () => {
    setGameStarted(false);
    setGameActive(false);
    setScore(0);
    setTimeLeft(30);
    setHoles(Array(9).fill(null));
    if (moleIntervalRef.current) clearInterval(moleIntervalRef.current);
    if (gameTimerRef.current) clearInterval(gameTimerRef.current);
  };

  return (
    <GameContainer>
      <HomeButton onClick={() => onGameOver(score)}>ホームに戻る</HomeButton>
      <h1>もぐらたたき</h1>
      <Score>スコア: {score}</Score>
      <Timer>残り時間: {timeLeft}秒</Timer>
      
      {!gameStarted ? (
        <Button onClick={startGame}>ゲーム開始</Button>
      ) : (
        <>
          <GameBoard>
            {holes.map((hole, index) => (
              <MoleHole key={index} onClick={() => whackHole(index)}>
                {hole === 'mole' && <Mole isVisible={true} />}
                {hole === 'bomb' && <Bomb isVisible={true} />}
              </MoleHole>
            ))}
          </GameBoard>
          
          {!gameActive && timeLeft === 0 && (
            <>
              <GameOver>ゲーム終了！</GameOver>
              <Button onClick={resetGame}>もう一度プレイ</Button>
            </>
          )}
        </>
      )}
    </GameContainer>
  );
};

export default WhackAMoleGame; 